/**
 * Simple Scrolly Triggering System
 * IntersectionObserver„Çí‰ΩøÁî®„Åó„Åü„Ç∑„É≥„Éó„É´„Å™„Çπ„ÇØ„É≠„Éº„É´„Éà„É™„Ç¨„É™„É≥„Ç∞„Ç∑„Çπ„ÉÜ„É†
 * 
 * ÂæìÊù•„ÅÆ„Çπ„ÇØ„É≠„Éº„É´„Ç§„Éô„É≥„Éà„Å®Áï∞„Å™„Çä„ÄÅIntersectionObserver„ÅØË¶ÅÁ¥†„ÅÆ„Éì„É•„Éº„Éù„Éº„Éà„Å®„ÅÆ‰∫§Â∑Æ„Çí
 * ÂäπÁéáÁöÑ„Å´Áõ£Ë¶ñ„Åó„Åæ„Åô„ÄÇ„Åì„Çå„Å´„Çà„Çä„ÄÅ„Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„ÅÆÂêë‰∏ä„Å®„Éê„ÉÉ„ÉÜ„É™„ÉºÊ∂àË≤ª„ÅÆÂâäÊ∏õ„ÇíÂÆüÁèæ„Åó„Åæ„Åô„ÄÇ
 * 
 * @class ScrollySystem
 * 
 * @example
 * // „Ç≥„Éº„É´„Éê„ÉÉ„ÇØÊñπÂºèÔºàÊé®Â•®Ôºâ
 * const scrolly = new ScrollySystem({
 *   threshold: 0.5,
 *   onChange: ({ current, previous }) => {
 *     console.log(`${previous} „Åã„Çâ ${current} „Å´Â§âÊõ¥`);
 *   }
 * });
 */
class ScrollySystem {
    /**
     * ScrollySystem„ÅÆ„Ç≥„É≥„Çπ„Éà„É©„ÇØ„Çø
     * 
     * @param {Object} options - Ë®≠ÂÆö„Ç™„Éó„Ç∑„Éß„É≥
     * @param {string} [options.selector='[data-trigger]'] - Áõ£Ë¶ñÂØæË±°Ë¶ÅÁ¥†„ÅÆCSS„Çª„É¨„ÇØ„Çø
     * @param {string} [options.triggerAttribute='data-trigger'] - „Éà„É™„Ç¨„ÉºID„ÇíÊ†ºÁ¥ç„Åô„ÇãÂ±ûÊÄßÂêç
     * @param {number|number[]} [options.threshold=0] - ‰∫§Â∑Æ„ÅÆÈñæÂÄ§Ôºà0-1„ÅÆÂÄ§„ÄÅ„Åæ„Åü„ÅØÈÖçÂàóÔºâ
     * @param {string} [options.rootMargin='0px'] - „É´„Éº„ÉàË¶ÅÁ¥†„ÅÆ„Éû„Éº„Ç∏„É≥ÔºàCSS marginÂΩ¢ÂºèÔºâ
     * @param {number} [options.debounceDelay=10] - „Éá„Éê„Ç¶„É≥„ÇπÈÅÖÂª∂ÊôÇÈñìÔºà„Éü„É™ÁßíÔºâ
     * @param {Function} [options.onChange=null] - „Éà„É™„Ç¨„ÉºÂ§âÊõ¥ÊôÇ„ÅÆ„Ç≥„Éº„É´„Éê„ÉÉ„ÇØÈñ¢Êï∞
     * @param {boolean} [options.debug=false] - „Éá„Éê„ÉÉ„Ç∞„É¢„Éº„Éâ„ÅÆÊúâÂäπÂåñ
     */
    constructor(options = {}) {
        // „Éá„Éï„Ç©„É´„ÉàË®≠ÂÆö
        this.config = {
            selector: '[data-trigger]',        // Áõ£Ë¶ñÂØæË±°„ÅÆ„Çª„É¨„ÇØ„Çø
            triggerAttribute: 'data-trigger',  // „Éà„É™„Ç¨„ÉºIDÂ±ûÊÄßÂêç
            threshold: 0,                      // ‰∫§Â∑Æ„ÅÆÈñæÂÄ§
            rootMargin: '0px',                 // „É´„Éº„Éà„Éû„Éº„Ç∏„É≥
            debounceDelay: 10,                 // „Éá„Éê„Ç¶„É≥„ÇπÈÅÖÂª∂ÊôÇÈñì(ms)
            onChange: null,                    // „Ç´„Çπ„Çø„É†„Ç≥„Éº„É´„Éê„ÉÉ„ÇØ
            debug: false,                      // „Éá„Éê„ÉÉ„Ç∞„É¢„Éº„Éâ
            ...options
        };
        
        this.observer = null;
        this.visibleElements = new Map();
        this.currentTriggerId = null;
        this.lastValidTriggerId = null;
        this._debounceTimer = null;           // „Çø„Ç§„Éû„ÉºÁÆ°ÁêÜÁî®
        this.instanceId = `scrolly-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`; // „É¶„Éã„Éº„ÇØ„Å™„Ç§„É≥„Çπ„Çø„É≥„ÇπID
        this._diagnosticCache = new Map();    // Ë®∫Êñ≠ÁµêÊûú„ÅÆ„Ç≠„É£„ÉÉ„Ç∑„É•
        
        this.init();
    }
    
    /**
     * „Ç∑„Çπ„ÉÜ„É†„ÅÆÂàùÊúüÂåñ
     * „Éá„Éê„Ç¶„É≥„ÇπÈñ¢Êï∞„ÅÆÊ∫ñÂÇô„Å®DOM„ÅÆÊ∫ñÂÇôÁä∂ÊÖã„Å´Âøú„Åò„ÅüÂá¶ÁêÜ„ÇíË°å„ÅÑ„Åæ„Åô
     * 
     * @private
     */
    init() {
        // „Éá„Éê„Ç¶„É≥„ÇπÈñ¢Êï∞„ÅÆÊ∫ñÂÇô
        this.debouncedUpdate = this.debounce(
            () => this.updateCurrentTrigger(), 
            this.config.debounceDelay
        );
        
        // DOM„ÅåÊ∫ñÂÇô„Åß„Åç„Åü„ÇâÈñãÂßã
        if (document.readyState === 'loading') {
            document.addEventListener('DOMContentLoaded', () => this.setup());
        } else {
            this.setup();
        }
    }
    
    /**
     * IntersectionObserver„ÅÆ„Çª„ÉÉ„Éà„Ç¢„ÉÉ„Éó„Å®ÂàùÊúüË©ï‰æ°
     * Observer„Å®DOMË¶ÅÁ¥†„ÅÆÁõ£Ë¶ñ„ÇíÈñãÂßã„Åó„ÄÅÂàùÊúüÁä∂ÊÖã„ÇíË©ï‰æ°„Åó„Åæ„Åô
     * 
     * @private
     */
    setup() {
        this.setupObserver();
        // ÂàùÊúüÁä∂ÊÖã„ÇíÂç≥Â∫ß„Å´Ë©ï‰æ°
        this.updateCurrentTrigger();
    }
    
    /**
     * IntersectionObserver„ÅÆ‰ΩúÊàê„Å®Ë®≠ÂÆö
     * 
     * IntersectionObserver„ÅØË¶ÅÁ¥†„Åå„Éì„É•„Éº„Éù„Éº„Éà„Å®‰∫§Â∑Æ„Åô„ÇãÁû¨Èñì„ÇíÂäπÁéáÁöÑ„Å´Ê§úÂá∫„Åó„Åæ„Åô„ÄÇ
     * ÂæìÊù•„ÅÆscroll„Ç§„Éô„É≥„Éà„Å®ÊØîËºÉ„Åó„Å¶‰ª•‰∏ã„ÅÆÂà©ÁÇπ„Åå„ÅÇ„Çä„Åæ„ÅôÔºö
     * - „É°„Ç§„É≥„Çπ„É¨„ÉÉ„Éâ„Çí„Éñ„É≠„ÉÉ„ÇØ„Åó„Å™„ÅÑÈùûÂêåÊúüÂá¶ÁêÜ
     * - „Éñ„É©„Ç¶„Ç∂„Å´„Çà„ÇãÊúÄÈÅ©ÂåñÔºà„Éï„É¨„Éº„É†Âçò‰Ωç„Åß„ÅÆÂá¶ÁêÜÔºâ
     * - „Éê„ÉÉ„ÉÜ„É™„ÉºÊ∂àË≤ª„ÅÆÂâäÊ∏õ
     * 
     * @private
     */
    setupObserver() {
        const options = {
            threshold: this.config.threshold,
            rootMargin: this.config.rootMargin
        };
        
        // IntersectionObserver„ÅÆ„Ç≥„Éº„É´„Éê„ÉÉ„ÇØÈñ¢Êï∞
        // entries: ‰∫§Â∑ÆÁä∂ÊÖã„ÅåÂ§âÂåñ„Åó„ÅüË¶ÅÁ¥†„ÅÆÈÖçÂàó
        this.observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                const element = entry.target;
                const triggerId = element.getAttribute(this.config.triggerAttribute);
                
                // entry.isIntersecting: Ë¶ÅÁ¥†„Åå„Éì„É•„Éº„Éù„Éº„Éà„Å®‰∫§Â∑Æ„Åó„Å¶„ÅÑ„Çã„Åã„Å©„ÅÜ„Åã
                // thresholdË®≠ÂÆö„Å´Âü∫„Å•„ÅÑ„Å¶‰∫§Â∑ÆÂà§ÂÆö„ÅåË°å„Çè„Çå„Çã
                if (entry.isIntersecting) {
                    // Ë¶ÅÁ¥†„ÅåÁîªÈù¢ÂÜÖ„Å´ÂÖ•„Å£„ÅüÂ†¥Âêà„ÄÅMap„Å´ËøΩÂä†
                    this.visibleElements.set(element, triggerId);
                } else {
                    // Ë¶ÅÁ¥†„ÅåÁîªÈù¢Â§ñ„Å´Âá∫„ÅüÂ†¥Âêà„ÄÅMap„Åã„ÇâÂâäÈô§
                    this.visibleElements.delete(element);
                }
            });
            
            // „Éá„Éê„Ç¶„É≥„ÇπÂá¶ÁêÜ„ÇíÈÅ©Áî®„Åó„Å¶„Éà„É™„Ç¨„ÉºÊõ¥Êñ∞„ÇíÂÆüË°å
            // ÈÄ£Á∂ö„Åó„Åü‰∫§Â∑Æ„Ç§„Éô„É≥„Éà„Å´„Çà„Çã„Éë„Éï„Ç©„Éº„Éû„É≥„Çπ‰Ωé‰∏ã„ÇíÈò≤„Åê
            this.debouncedUpdate();
        }, options);
        
        // Áõ£Ë¶ñÂØæË±°„ÅÆË¶ÅÁ¥†„ÇíÁôªÈå≤
        const targets = document.querySelectorAll(this.config.selector);
        targets.forEach(target => this.observer.observe(target));
    }
    
    /**
     * ÁèæÂú®„Ç¢„ÇØ„ÉÜ„Ç£„Éñ„Å™„Éà„É™„Ç¨„Éº„ÅÆÊõ¥Êñ∞
     * 
     * ÁîªÈù¢ÂÜÖ„Å´Ë°®Á§∫„Åï„Çå„Å¶„ÅÑ„ÇãË¶ÅÁ¥†„ÅÆ‰∏≠„Åã„Çâ„ÄÅÊúÄ„ÇÇ‰∏ä„Å´‰ΩçÁΩÆ„Åô„ÇãË¶ÅÁ¥†„Çí
     * ÁèæÂú®„ÅÆ„Éà„É™„Ç¨„Éº„Å®„Åó„Å¶ÈÅ∏Êäû„Åó„Åæ„Åô„ÄÇ„Åì„Çå„Å´„Çà„Çä„ÄÅ„Çπ„ÇØ„É≠„Éº„É´‰ΩçÁΩÆ„Å´
     * Âøú„Åò„ÅüÈÅ©Âàá„Å™„Çª„ÇØ„Ç∑„Éß„É≥Âà§ÂÆö„ÇíÂÆüÁèæ„Åó„Åæ„Åô„ÄÇ
     * 
     * @private
     */
    updateCurrentTrigger() {
        const entries = Array.from(this.visibleElements.entries());
        
        if (entries.length > 0) {
            // ÁîªÈù¢ÂÜÖ„ÅßÊúÄ„ÇÇ‰∏ä„Å´„ÅÇ„ÇãË¶ÅÁ¥†„ÇíÂèñÂæó
            // getBoundingClientRect()„Åß„Éì„É•„Éº„Éù„Éº„ÉàÁõ∏ÂØæ‰ΩçÁΩÆ„ÇíÂèñÂæó„Åó„ÄÅ
            // topÂÄ§„ÅåÊúÄÂ∞èÔºàÊúÄ‰∏ä‰ΩçÔºâ„ÅÆË¶ÅÁ¥†„ÇíÈÅ∏Êäû„Åô„Çã
            const topEntry = entries.reduce((prev, curr) => {
                const prevRect = prev[0].getBoundingClientRect();
                const currRect = curr[0].getBoundingClientRect();
                return currRect.top < prevRect.top ? curr : prev;
            });
            
            const triggerId = topEntry[1];
            this.lastValidTriggerId = triggerId;
            this.setCurrentTrigger(triggerId);
        } else {
            // ÁîªÈù¢ÂÜÖ„Å´Ë¶ÅÁ¥†„Åå„Å™„ÅÑÂ†¥ÂêàÔºà„Åô„Åπ„Å¶„ÅÆË¶ÅÁ¥†„ÅåÁîªÈù¢Â§ñÔºâ
            // „Åì„ÅÆ„Ç±„Éº„Çπ„ÅØ„ÄÅË¶ÅÁ¥†Èñì„ÅÆ„Çπ„Éö„Éº„Çπ„ÅåÂ§ß„Åç„ÅÑÂ†¥Âêà„ÇÑ
            // È´òÈÄü„Çπ„ÇØ„É≠„Éº„É´ÊôÇ„Å´Áô∫Áîü„Åô„ÇãÂèØËÉΩÊÄß„Åå„ÅÇ„Çã
            if (this.lastValidTriggerId !== null) {
                // ÊúÄÂæå„ÅÆÊúâÂäπ„Å™„Éà„É™„Ç¨„Éº„ÇíÁ∂≠ÊåÅ„Åô„Çã„Åì„Å®„Åß„ÄÅ
                // „Éà„É™„Ç¨„Éº„ÅåÁ™ÅÁÑ∂Ê∂àÂ§±„Åô„Çã„Åì„Å®„ÇíÈò≤„Åê
                this.setCurrentTrigger(this.lastValidTriggerId);
            }
        }
    }
    
    /**
     * ÁèæÂú®„ÅÆ„Éà„É™„Ç¨„ÉºID„ÇíË®≠ÂÆö„Åó„ÄÅÂ§âÊõ¥„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
     * 
     * @param {string} triggerId - Ë®≠ÂÆö„Åô„Çã„Éà„É™„Ç¨„ÉºID
     * @private
     */
    setCurrentTrigger(triggerId) {
        if (this.currentTriggerId !== triggerId) {
            const previousTriggerId = this.currentTriggerId;
            this.currentTriggerId = triggerId;
            
            // „Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
            this.emitTriggerChange(triggerId, previousTriggerId);
        }
    }
    
    /**
     * „Éà„É™„Ç¨„ÉºÂ§âÊõ¥„Ç§„Éô„É≥„Éà„ÅÆÁô∫ÁÅ´
     * 
     * „Ç≥„Éº„É´„Éê„ÉÉ„ÇØÈñ¢Êï∞„Å®„Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„ÅÆ‰∏°Êñπ„ÇíÁô∫ÁÅ´„Åô„Çã„Åì„Å®„Åß„ÄÅ
     * Áï∞„Å™„Çã‰ΩøÁî®„Éë„Çø„Éº„É≥„Å´ÂØæÂøú„Åó„Åæ„Åô„ÄÇ
     * 
     * @param {string} currentId - ÁèæÂú®„ÅÆ„Éà„É™„Ç¨„ÉºID
     * @param {string} previousId - ÂâçÂõû„ÅÆ„Éà„É™„Ç¨„ÉºID
     * @private
     */
    emitTriggerChange(currentId, previousId) {
        const detail = {
            current: currentId,
            previous: previousId,
            timestamp: Date.now(),
            instanceId: this.instanceId
        };
        
        // „Ç´„Çπ„Çø„É†„Ç≥„Éº„É´„Éê„ÉÉ„ÇØ„Åå„ÅÇ„Çå„Å∞ÂÆüË°å
        if (typeof this.config.onChange === 'function') {
            this.config.onChange(detail);
        }
        
        // „Ç§„Éô„É≥„Éà„ÇÇÁô∫ÁÅ´Ôºà‰∫íÊèõÊÄß„ÅÆ„Åü„ÇÅÔºâ
        const event = new CustomEvent('scrollTrigger', { detail });
        window.dispatchEvent(event);
    }
    
    /**
     * „Éá„Éê„Ç¶„É≥„ÇπÈñ¢Êï∞„ÅÆ„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£
     * 
     * Áü≠ÊôÇÈñì„Å´ÈÄ£Á∂ö„Åó„Å¶Âëº„Å≥Âá∫„Åï„Çå„ÇãÈñ¢Êï∞„ÅÆÂÆüË°å„ÇíÂà∂Èôê„Åó„ÄÅ
     * ÊúÄÂæå„ÅÆÂëº„Å≥Âá∫„Åó„Åã„ÇâÊåáÂÆöÊôÇÈñìÂæå„Å´‰∏ÄÂ∫¶„Å†„ÅëÂÆüË°å„Åó„Åæ„Åô„ÄÇ
     * „Åì„Çå„Å´„Çà„Çä„ÄÅIntersectionObserver„Åã„Çâ„ÅÆÈ†ªÁπÅ„Å™„Ç≥„Éº„É´„Éê„ÉÉ„ÇØ„Å´„Çà„Çã
     * „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ‰Ωé‰∏ã„ÇíÈò≤„Åé„Åæ„Åô„ÄÇ
     * 
     * @param {Function} func - „Éá„Éê„Ç¶„É≥„ÇπÂØæË±°„ÅÆÈñ¢Êï∞
     * @param {number} wait - ÂæÖÊ©üÊôÇÈñìÔºà„Éü„É™ÁßíÔºâ
     * @returns {Function} „Éá„Éê„Ç¶„É≥„ÇπÂá¶ÁêÜ„ÅåÈÅ©Áî®„Åï„Çå„ÅüÈñ¢Êï∞
     * @private
     */
    debounce(func, wait) {
        return (...args) => {
            clearTimeout(this._debounceTimer);
            this._debounceTimer = setTimeout(() => func.apply(this, args), wait);
        };
    }
    
    /**
     * ÁèæÂú®„ÅÆ„Éà„É™„Ç¨„ÉºID„ÇíÂèñÂæó
     * 
     * @returns {string|null} ÁèæÂú®„ÅÆ„Éà„É™„Ç¨„ÉºID„ÄÅ„Åæ„Åü„ÅØÊú™Ë®≠ÂÆö„ÅÆÂ†¥Âêà„ÅØnull
     * @public
     */
    getCurrentTriggerId() {
        return this.currentTriggerId;
    }
    
    /**
     * Ë¶ÅÁ¥†„ÇíÂãïÁöÑ„Å´Áõ£Ë¶ñÂØæË±°„Å´ËøΩÂä†
     * 
     * „Éö„Éº„Ç∏„ÅÆÂãïÁöÑ„Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÇÑÈÅÖÂª∂Ë™≠„ÅøËæº„Åø„Åï„Çå„ÅüË¶ÅÁ¥†„Çí
     * Âæå„Åã„ÇâÁõ£Ë¶ñÂØæË±°„Å´ËøΩÂä†„Åô„ÇãÈöõ„Å´‰ΩøÁî®„Åó„Åæ„Åô„ÄÇ
     * 
     * @param {Element} element - Áõ£Ë¶ñÂØæË±°„Å´ËøΩÂä†„Åô„ÇãË¶ÅÁ¥†
     * @public
     */
    observe(element) {
        if (this.observer && element) {
            this.observer.observe(element);
        }
    }
    
    /**
     * Ë¶ÅÁ¥†„ÇíÂãïÁöÑ„Å´Áõ£Ë¶ñÂØæË±°„Åã„ÇâÂâäÈô§
     * 
     * ‰∏çË¶Å„Å´„Å™„Å£„ÅüË¶ÅÁ¥†„ÇíÁõ£Ë¶ñÂØæË±°„Åã„ÇâÈô§Â§ñ„Åó„ÄÅ
     * „É°„É¢„É™„É™„Éº„ÇØ„ÇíÈò≤„Åé„Åæ„Åô„ÄÇ
     * 
     * @param {Element} element - Áõ£Ë¶ñÂØæË±°„Åã„ÇâÂâäÈô§„Åô„ÇãË¶ÅÁ¥†
     * @public
     */
    unobserve(element) {
        if (this.observer && element) {
            this.observer.unobserve(element);
            this.visibleElements.delete(element);
        }
    }
    
    /**
     * „Ç∑„Çπ„ÉÜ„É†„ÅÆÂÆåÂÖ®„Å™Á†¥Ê£Ñ„Å®„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó
     * 
     * „Åô„Åπ„Å¶„ÅÆ„É™„ÇΩ„Éº„ÇπÔºà„Çø„Ç§„Éû„Éº„ÄÅObserver„ÄÅÂÜÖÈÉ®Áä∂ÊÖãÔºâ„ÇíËß£Êîæ„Åó„ÄÅ
     * „É°„É¢„É™„É™„Éº„ÇØ„ÇíÈò≤„Åé„Åæ„Åô„ÄÇSPAÁí∞Â¢É„Åß„ÅÆ„Éö„Éº„Ç∏ÈÅ∑ÁßªÊôÇ„ÇÑ„ÄÅ
     * „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆ„Ç¢„É≥„Éû„Ç¶„É≥„ÉàÊôÇ„Å´Âëº„Å≥Âá∫„Åô„Åì„Å®„ÇíÊé®Â•®„Åó„Åæ„Åô„ÄÇ
     * 
     * @public
     */
    destroy() {
        // „Çø„Ç§„Éû„Éº„Çí„ÇØ„É™„Ç¢
        if (this._debounceTimer) {
            clearTimeout(this._debounceTimer);
        }
        
        // IntersectionObserver„ÇíÂÆåÂÖ®„Å´ÂàáÊñ≠
        // disconnect()„Åß„Åô„Åπ„Å¶„ÅÆÁõ£Ë¶ñÂØæË±°„ÅåËß£Èô§„Åï„Çå„Çã
        if (this.observer) {
            this.observer.disconnect();
        }
        
        // ÂÜÖÈÉ®Áä∂ÊÖã„Çí„ÇØ„É™„Ç¢
        this.visibleElements.clear();
        this.currentTriggerId = null;
        this.lastValidTriggerId = null;
        this._diagnosticCache.clear();
    }

    /**
     * „Ç∑„Çπ„ÉÜ„É†ÂÖ®‰Ωì„ÅÆË®∫Êñ≠„ÇíÂÆüË°å
     * 
     * HTMLÊßãÁØâÊôÇ„ÅÆÊ≥®ÊÑèÁÇπ„Çí„ÉÅ„Çß„ÉÉ„ÇØ„Åó„ÄÅÂïèÈ°å„ÇíÁâπÂÆö„Åó„Åæ„Åô„ÄÇ
     * ÈñãÁô∫ÊôÇ„ÅÆ„Éá„Éê„ÉÉ„Ç∞„Å´ÊúâÁî®„Åß„ÄÅ„Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÂïèÈ°å„ÇÑË®≠ÂÆö„Éü„Çπ„ÇíÊ§úÂá∫„Åß„Åç„Åæ„Åô„ÄÇ
     * 
     * @param {boolean} [verbose=false] - Ë©≥Á¥∞„Å™Ë®∫Êñ≠ÁµêÊûú„ÇíÂá∫Âäõ„Åô„Çã„Åã„Å©„ÅÜ„Åã
     * @returns {Object} Ë®∫Êñ≠ÁµêÊûú„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
     * @public
     */
    diagnose(verbose = false) {
        const cacheKey = `diagnose_${verbose}`;
        if (this._diagnosticCache.has(cacheKey)) {
            return this._diagnosticCache.get(cacheKey);
        }

        const issues = [];
        const elements = document.querySelectorAll(this.config.selector);
        
        // Ë¶ÅÁ¥†Èñ¢ÈÄ£„ÅÆË®∫Êñ≠
        const elementIssues = this.validateElements();
        issues.push(...elementIssues);
        
        // „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÈñ¢ÈÄ£„ÅÆË®∫Êñ≠
        const performanceIssues = this.checkPerformance();
        issues.push(...performanceIssues);
        
        // Áí∞Â¢ÉÈñ¢ÈÄ£„ÅÆË®∫Êñ≠
        const environmentIssues = this._checkEnvironment();
        issues.push(...environmentIssues);

        // Ë®∫Êñ≠ÁµêÊûú„ÅÆ‰ΩúÊàê
        const result = {
            status: this._determineStatus(issues),
            issues: issues,
            summary: {
                totalElements: elements.length,
                problematicElements: this._countProblematicElements(issues),
                performanceScore: this._calculatePerformanceScore(issues),
                timestamp: Date.now()
            }
        };

        // „Éá„Éê„ÉÉ„Ç∞„É¢„Éº„Éâ„Åæ„Åü„ÅØverbose„É¢„Éº„Éâ„ÅÆÂ†¥Âêà„ÄÅ„Ç≥„É≥„ÇΩ„Éº„É´„Å´Âá∫Âäõ
        if (this.config.debug || verbose) {
            this._outputDiagnosticResults(result);
        }

        // ÁµêÊûú„Çí„Ç≠„É£„ÉÉ„Ç∑„É•Ôºà5ÁßíÈñìÔºâ
        this._diagnosticCache.set(cacheKey, result);
        setTimeout(() => {
            this._diagnosticCache.delete(cacheKey);
        }, 5000);

        return result;
    }

    /**
     * Áõ£Ë¶ñÂØæË±°Ë¶ÅÁ¥†„ÅÆÂ¶•ÂΩìÊÄß„Çí„ÉÅ„Çß„ÉÉ„ÇØ
     * 
     * @returns {Array} Ë¶ÅÁ¥†Èñ¢ÈÄ£„ÅÆÂïèÈ°åÈÖçÂàó
     * @private
     */
    validateElements() {
        const issues = [];
        const elements = document.querySelectorAll(this.config.selector);
        const triggerIds = new Set();
        const viewportHeight = window.innerHeight;

        if (elements.length === 0) {
            issues.push({
                type: 'no_elements',
                severity: 'error',
                message: 'Áõ£Ë¶ñÂØæË±°Ë¶ÅÁ¥†„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì',
                suggestion: `„Çª„É¨„ÇØ„Çø "${this.config.selector}" „Å´‰∏ÄËá¥„Åô„ÇãË¶ÅÁ¥†„ÇíËøΩÂä†„Åó„Å¶„Åè„Å†„Åï„ÅÑ`
            });
            return issues;
        }

        elements.forEach((element, index) => {
            const triggerId = element.getAttribute(this.config.triggerAttribute);
            const rect = element.getBoundingClientRect();
            const styles = window.getComputedStyle(element);

            // data-triggerÂ±ûÊÄß„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
            if (!triggerId) {
                issues.push({
                    type: 'missing_trigger_id',
                    severity: 'error',
                    message: `Ë¶ÅÁ¥† ${index + 1} „Å´„Éà„É™„Ç¨„ÉºIDÂ±ûÊÄß„Åå„ÅÇ„Çä„Åæ„Åõ„Çì`,
                    element: element,
                    suggestion: `${this.config.triggerAttribute}="unique-id" „ÇíËøΩÂä†„Åó„Å¶„Åè„Å†„Åï„ÅÑ`
                });
            } else if (triggerIds.has(triggerId)) {
                issues.push({
                    type: 'duplicate_trigger_id',
                    severity: 'error',
                    message: `„Éà„É™„Ç¨„ÉºID "${triggerId}" „ÅåÈáçË§á„Åó„Å¶„ÅÑ„Åæ„Åô`,
                    element: element,
                    suggestion: 'ÂêÑË¶ÅÁ¥†„Å´„ÅØ‰∏ÄÊÑè„ÅÆ„Éà„É™„Ç¨„ÉºID„ÇíË®≠ÂÆö„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
                });
            } else {
                triggerIds.add(triggerId);
            }

            // Ë¶ÅÁ¥†„ÅÆÈ´ò„Åï„ÉÅ„Çß„ÉÉ„ÇØ
            const elementHeight = element.offsetHeight;
            if (elementHeight < viewportHeight * 0.5) {
                issues.push({
                    type: 'insufficient_height',
                    severity: 'warning',
                    message: `Ë¶ÅÁ¥† "${triggerId || index + 1}" „ÅÆÈ´ò„Åï„Åå‰∏çÂçÅÂàÜ„Åß„Åô (${elementHeight}px)`,
                    element: element,
                    suggestion: 'min-height: 100vh „Åæ„Åü„ÅØ„Åù„Çå‰ª•‰∏ä„ÅÆÈ´ò„Åï„ÇíË®≠ÂÆö„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
                });
            }

            // ÂèØË¶ñÊÄß„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
            if (styles.display === 'none') {
                issues.push({
                    type: 'element_hidden',
                    severity: 'error',
                    message: `Ë¶ÅÁ¥† "${triggerId || index + 1}" „Åå display: none „ÅßÈùûË°®Á§∫„Åß„Åô`,
                    element: element,
                    suggestion: 'display: none „ÅÆ‰ª£„Çè„Çä„Å´ visibility: hidden „Åæ„Åü„ÅØ opacity: 0 „Çí‰ΩøÁî®„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
                });
            } else if (styles.visibility === 'hidden') {
                issues.push({
                    type: 'element_visibility_hidden',
                    severity: 'warning',
                    message: `Ë¶ÅÁ¥† "${triggerId || index + 1}" „Åå visibility: hidden „Åß„Åô`,
                    element: element,
                    suggestion: 'IntersectionObserver„ÅØÁõ£Ë¶ñ„Åó„Åæ„Åô„Åå„ÄÅ„É¶„Éº„Ç∂„Éº„Å´„ÅØË¶ã„Åà„Åæ„Åõ„Çì'
                });
            }

            // position: fixed „ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
            if (styles.position === 'fixed') {
                issues.push({
                    type: 'fixed_position',
                    severity: 'warning',
                    message: `Ë¶ÅÁ¥† "${triggerId || index + 1}" „Åå position: fixed „Åß„Åô`,
                    element: element,
                    suggestion: '„Çπ„ÇØ„É≠„Éº„É´‰ΩçÁΩÆ„ÅÆË®àÁÆó„Å´ÂΩ±Èüø„Åô„ÇãÂèØËÉΩÊÄß„Åå„ÅÇ„Çä„Åæ„Åô'
                });
            }
        });

        return issues;
    }

    /**
     * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÈñ¢ÈÄ£„ÅÆÂïèÈ°å„Çí„ÉÅ„Çß„ÉÉ„ÇØ
     * 
     * @returns {Array} „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÈñ¢ÈÄ£„ÅÆÂïèÈ°åÈÖçÂàó
     * @private
     */
    checkPerformance() {
        const issues = [];
        const elements = document.querySelectorAll(this.config.selector);

        // Ë¶ÅÁ¥†Êï∞„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        if (elements.length > 50) {
            issues.push({
                type: 'too_many_elements',
                severity: 'warning',
                message: `Áõ£Ë¶ñÂØæË±°Ë¶ÅÁ¥†„ÅåÂ§ö„Åô„Åé„Åæ„Åô (${elements.length}ÂÄã)`,
                suggestion: '50ÂÄã‰ª•‰∏ã„Å´Êäë„Åà„Çã„Åã„ÄÅÊÆµÈöéÁöÑ„Å™Ë¶ÅÁ¥†ËøΩÂä†„ÇíÊ§úË®é„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
            });
        }

        // thresholdË®≠ÂÆö„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        if (Array.isArray(this.config.threshold) && this.config.threshold.length > 5) {
            issues.push({
                type: 'complex_threshold',
                severity: 'warning',
                message: `thresholdÈÖçÂàó„ÅåË§áÈõë„Åô„Åé„Åæ„Åô (${this.config.threshold.length}ÂÄã„ÅÆÂÄ§)`,
                suggestion: 'Âçò‰∏Ä„ÅÆÂÄ§„Åæ„Åü„ÅØÊúÄÂ§ß5ÂÄã„Åæ„Åß„ÅÆÈÖçÂàó„ÇíÊé®Â•®„Åó„Åæ„Åô'
            });
        } else if (this.config.threshold === 1.0) {
            issues.push({
                type: 'high_threshold',
                severity: 'warning',
                message: 'threshold: 1.0 „ÅØË¶ÅÁ¥†„ÅåÂÆåÂÖ®„Å´Ë°®Á§∫„Åï„Çå„Çã„Åæ„ÅßÁô∫ÁÅ´„Åó„Åæ„Åõ„Çì',
                suggestion: '0.1 - 0.5 Á®ãÂ∫¶„ÅÆÂÄ§„ÇíÊé®Â•®„Åó„Åæ„Åô'
            });
        }

        // debounceDelay „ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        if (this.config.debounceDelay > 100) {
            issues.push({
                type: 'high_debounce',
                severity: 'info',
                message: `debounceDelay „ÅåÂ§ß„Åç„Åô„Åé„ÇãÂèØËÉΩÊÄß„Åå„ÅÇ„Çä„Åæ„Åô (${this.config.debounceDelay}ms)`,
                suggestion: 'ÂøúÁ≠îÊÄß„ÇíÈáçË¶ñ„Åô„ÇãÂ†¥Âêà„ÅØ50ms‰ª•‰∏ã„ÇíÊé®Â•®„Åó„Åæ„Åô'
            });
        } else if (this.config.debounceDelay < 5) {
            issues.push({
                type: 'low_debounce',
                severity: 'info',
                message: `debounceDelay „ÅåÂ∞è„Åï„Åô„Åé„ÇãÂèØËÉΩÊÄß„Åå„ÅÇ„Çä„Åæ„Åô (${this.config.debounceDelay}ms)`,
                suggestion: '„Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„ÇíÈáçË¶ñ„Åô„ÇãÂ†¥Âêà„ÅØ10ms‰ª•‰∏ä„ÇíÊé®Â•®„Åó„Åæ„Åô'
            });
        }

        return issues;
    }

    /**
     * Áí∞Â¢ÉÈñ¢ÈÄ£„ÅÆÂïèÈ°å„Çí„ÉÅ„Çß„ÉÉ„ÇØ
     * 
     * @returns {Array} Áí∞Â¢ÉÈñ¢ÈÄ£„ÅÆÂïèÈ°åÈÖçÂàó
     * @private
     */
    _checkEnvironment() {
        const issues = [];

        // „Çπ„ÇØ„É≠„Éº„É´ÂèØËÉΩÊÄß„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        const documentHeight = Math.max(
            document.body.scrollHeight,
            document.documentElement.scrollHeight
        );
        const viewportHeight = window.innerHeight;

        if (documentHeight <= viewportHeight) {
            issues.push({
                type: 'no_scroll',
                severity: 'error',
                message: '„Éö„Éº„Ç∏„Åå„Çπ„ÇØ„É≠„Éº„É´‰∏çÂèØËÉΩ„Åß„Åô',
                suggestion: '„Ç≥„É≥„ÉÜ„É≥„ÉÑ„ÅÆÈ´ò„Åï„ÇíÂ¢ó„ÇÑ„Åô„Åã„ÄÅoverflowË®≠ÂÆö„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
            });
        }

        // overflowË®≠ÂÆö„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        const bodyStyles = window.getComputedStyle(document.body);
        const htmlStyles = window.getComputedStyle(document.documentElement);

        if (bodyStyles.overflow === 'hidden' || htmlStyles.overflow === 'hidden') {
            issues.push({
                type: 'overflow_hidden',
                severity: 'error',
                message: 'body „Åæ„Åü„ÅØ html „Å´ overflow: hidden „ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åô',
                suggestion: 'overflow-x: hidden „ÅÆ„Åø„Çí‰ΩøÁî®„Åô„Çã„Åã„ÄÅoverflowË®≠ÂÆö„ÇíÂâäÈô§„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
            });
        }

        // „Éì„É•„Éº„Éù„Éº„Éà„É°„Çø„Çø„Ç∞„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØÔºà„É¢„Éê„Ç§„É´ÂØæÂøúÔºâ
        const viewportMeta = document.querySelector('meta[name="viewport"]');
        if (!viewportMeta) {
            issues.push({
                type: 'no_viewport_meta',
                severity: 'warning',
                message: '„Éì„É•„Éº„Éù„Éº„Éà„É°„Çø„Çø„Ç∞„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì',
                suggestion: '<meta name="viewport" content="width=device-width, initial-scale=1.0"> „ÇíËøΩÂä†„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
            });
        }

        // IntersectionObserver „Çµ„Éù„Éº„Éà„ÅÆ„ÉÅ„Çß„ÉÉ„ÇØ
        if (!window.IntersectionObserver) {
            issues.push({
                type: 'no_intersection_observer',
                severity: 'error',
                message: 'IntersectionObserver„Åå„Çµ„Éù„Éº„Éà„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì',
                suggestion: '„Éù„É™„Éï„Ç£„É´„ÇíËøΩÂä†„Åô„Çã„Åã„ÄÅÂØæÂøú„Éñ„É©„Ç¶„Ç∂„Çí‰ΩøÁî®„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
            });
        }

        return issues;
    }

    /**
     * ÂïèÈ°å„ÅÆÈáçË¶ÅÂ∫¶„Åã„ÇâÂÖ®‰Ωì„ÅÆ„Çπ„ÉÜ„Éº„Çø„Çπ„ÇíÊ±∫ÂÆö
     * 
     * @param {Array} issues - ÂïèÈ°åÈÖçÂàó
     * @returns {string} „Çπ„ÉÜ„Éº„Çø„Çπ ('ok', 'warning', 'error')
     * @private
     */
    _determineStatus(issues) {
        if (issues.some(issue => issue.severity === 'error')) {
            return 'error';
        } else if (issues.some(issue => issue.severity === 'warning')) {
            return 'warning';
        }
        return 'ok';
    }

    /**
     * ÂïèÈ°å„ÅÆ„ÅÇ„ÇãË¶ÅÁ¥†Êï∞„Çí„Ç´„Ç¶„É≥„Éà
     * 
     * @param {Array} issues - ÂïèÈ°åÈÖçÂàó
     * @returns {number} ÂïèÈ°å„ÅÆ„ÅÇ„ÇãË¶ÅÁ¥†Êï∞
     * @private
     */
    _countProblematicElements(issues) {
        const problematicElements = new Set();
        issues.forEach(issue => {
            if (issue.element) {
                problematicElements.add(issue.element);
            }
        });
        return problematicElements.size;
    }

    /**
     * „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„Çπ„Ç≥„Ç¢„ÇíË®àÁÆó
     * 
     * @param {Array} issues - ÂïèÈ°åÈÖçÂàó
     * @returns {number} „Éë„Éï„Ç©„Éº„Éû„É≥„Çπ„Çπ„Ç≥„Ç¢ (0-10)
     * @private
     */
    _calculatePerformanceScore(issues) {
        let score = 10;
        issues.forEach(issue => {
            switch (issue.severity) {
                case 'error':
                    score -= 3;
                    break;
                case 'warning':
                    score -= 1;
                    break;
                case 'info':
                    score -= 0.5;
                    break;
            }
        });
        return Math.max(0, score);
    }

    /**
     * Ë®∫Êñ≠ÁµêÊûú„Çí„Ç≥„É≥„ÇΩ„Éº„É´„Å´Âá∫Âäõ
     * 
     * @param {Object} result - Ë®∫Êñ≠ÁµêÊûú
     * @private
     */
    _outputDiagnosticResults(result) {
        const statusEmoji = {
            'ok': '‚úÖ',
            'warning': '‚ö†Ô∏è',
            'error': '‚ùå'
        };

        console.group(`${statusEmoji[result.status]} ScrollySystem Ë®∫Êñ≠ÁµêÊûú (ID: ${this.instanceId.split('-').pop()})`);
        
        console.log('üìä Ê¶ÇË¶Å:', result.summary);
        
        if (result.issues.length > 0) {
            console.group('üîç Ê§úÂá∫„Åï„Çå„ÅüÂïèÈ°å:');
            result.issues.forEach((issue, index) => {
                const severityEmoji = {
                    'error': 'üö´',
                    'warning': '‚ö†Ô∏è',
                    'info': '‚ÑπÔ∏è'
                };
                console.log(`${severityEmoji[issue.severity]} ${issue.message}`);
                console.log(`   üí° ${issue.suggestion}`);
                if (issue.element) {
                    console.log('   üéØ Ë¶ÅÁ¥†:', issue.element);
                }
            });
            console.groupEnd();
        } else {
            console.log('‚ú® ÂïèÈ°å„ÅØÊ§úÂá∫„Åï„Çå„Åæ„Åõ„Çì„Åß„Åó„Åü');
        }
        
        console.groupEnd();
    }
}

// ES6 module „Å®„Åó„Å¶ export
export { ScrollySystem };